.admin_box {
  width: 100vw;
  height: 100vh;
}
.admin_box .header {
  display: block;
  background-color: #fff;
  color: #938d8d;
  line-height: 20px;
  padding: 0;
}
.admin_box .footer {
  text-align: center;
  padding: 40px 50px;
}
.hid {
  display: none;
}
.header_box {
  display: block;
  width: 100%;
  height: 100%;
}
.header_box .top {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 50%;
  width: 100%;
  padding-right: 20px;
  border-bottom: 2px solid #acb6da;
  cursor: pointer;
}
.header_box .top .tips {
  margin-left: 16px;
}
.header_box .bom {
  height: 50%;
  width: 100%;
  padding-right: 35px;
}
.header_box .bom .weather {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 25px;
  background-color: #ff7b00;
  border-radius: 4px;
  margin: 0 8px;
}
.header_box .bom .icon {
  display: flex;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.header_box .bom .rain {
  background-color: #1587e5;
}
.header_box .bom .rain .icon {
  background-image: url(data:image/png;base64,);
}
.header_box .bom .sun {
  background-color: #ff7b00;
}
.header_box .bom .sun .icon {
  background-image: url(data:image/png;base64,);
}
.header_box .bom .cloud {
  background-color: #1587e5;
}
.header_box .bom .cloud .icon {
  background-image: url(data:image/png;base64,);
}
.header_box .bom .title {
  position: relative;
  font-size: 20px;
  margin-left: 60px;
}
.header_box .bom .title::after {
  content: '';
  position: absolute;
  bottom: -260%;
  left: 50%;
  transform: translateX(-50%);
  border-bottom: 20px solid transparent;
  border-top: 20px solid #fff;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
}
.content {
  position: relative;
  padding: 30px 30px 0;
}
.content .full_btn {
  position: absolute;
  right: 0px;
  top: 0px ;
}
.content .box {
  width: 100%;
  height: 100%;
  background-color: #fff;
}
.slider {
  position: relative;
  padding-top: 100px;
}
.slider .title {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100px;
  background-color: #525664;
  color: #fff;
  padding-left: 20px;
}
.slider .title .logo {
  width: 50px;
  height: 45px;
  background-image: url(../../static/media/logo.b5aef53b.png);
  background-size: 100% 100%;
  margin-right: 4px;
}
.slider .title h1 {
  font-size: 20px;
  color: #d3cecc;
}
.cal_box {
  position: relative;
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  left: 50%;
  padding: 160px 30px 30px;
  transform: translateX(-50%);
  width: 70%;
  height: 600px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 10px 10px 16px -8px rgba(0, 0, 0, 0.1), -10px -10px 16px -8px rgba(165, 170, 187, 0.1);
}
.cal_box .button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: skyblue;
  cursor: pointer;
  font-family: Helvetica;
  font-size: 22px;
  color: #9396a4;
  font-weight: 500;
  box-shadow: 4px 4px 10px -8px rgba(0, 0, 0, 0.7), -4px -4px 10px -8px rgba(0, 0, 0, 0.7);
  background: linear-gradient(135deg, #f6f6f6 0%, #ffffff 100%);
}
.cal_box .button-0 {
  width: 128px;
  border-radius: 30px;
}
.cal_box .button:active {
  box-shadow: 1px 1px 1px -2px rgba(0, 0, 0, 0.7), -1px -1px 1px -2px rgba(0, 0, 0, 0.7);
}
.input_box {
  position: absolute;
  padding: 20px 30px 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 120px;
}
.input_box input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 22px;
  color: #586db7;
}
.cal_box .theme_color {
  color: #586db7;
}

.login {
  width: 100vw;
  height: 100vh;
  background-size: 100% 100%;
}
.header {
  display: flex;
  align-items: center;
  padding-left: 26px;
  width: 100%;
  height: 100px;
  color: #fff;
  background-color: rgba(82, 104, 178, 0.8);
}
.header .logo {
  width: 70px;
  height: 60px;
  background-image: url(../../static/media/logo.b5aef53b.png);
  background-size: 100% 100%;
  margin-right: 10px;
}
.header h1 {
  font-size: 25px;
  color: #fff;
}
.login_box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
}
.login_box h4 {
  font-size: 30px;
  text-align: center;
  font-weight: 700;
  color: #586db7;
  margin-bottom: 30px;
}
.login_box .login_btn {
  margin-top: 30px;
}


 html, body, div, span, applet, object, iframe,
 h1, h2, h3, h4, h5, h6, p, blockquote, pre,
 a, abbr, acronym, address, big, cite, code,
 del, dfn, em, img, ins, kbd, q, s, samp,
 small, strike, strong, sub, sup, tt, var,
 b, u, i, center,
 dl, dt, dd, ol, ul, li,
 fieldset, form, label, legend,
 table, caption, tbody, tfoot, thead, tr, th, td,
 article, aside, canvas, details, embed, 
 figure, figcaption, footer, header, hgroup, 
 menu, nav, output, ruby, section, summary,
 time, mark, audio, video{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
 }
 /* HTML5 display-role reset for older browsers */
 article, aside, details, figcaption, figure, 
 footer, header, hgroup, menu, nav, section{
   display: block;
 }
 ol, ul, li{
   list-style: none;
 }
 blockquote, q{
   quotes: none;
 }
 blockquote:before, blockquote:after,
 q:before, q:after{
   content: '';
   content: none;
 }
 table{
   border-collapse: collapse;
   border-spacing: 0;
 }
  
 /* custom */
 a{
   color: #7e8c8d;
   text-decoration: none;
   -webkit-backface-visibility: hidden;
 }
 ::-webkit-scrollbar{
   width: 5px;
   height: 5px;
 }
 ::-webkit-scrollbar-track-piece{
   background-color: rgba(0, 0, 0, 0.2);
   -webkit-border-radius: 6px;
 }
 ::-webkit-scrollbar-thumb:vertical{
   height: 5px;
   background-color: rgba(125, 125, 125, 0.7);
   -webkit-border-radius: 6px;
 }
 ::-webkit-scrollbar-thumb:horizontal{
   width: 5px;
   background-color: rgba(125, 125, 125, 0.7);
   -webkit-border-radius: 6px;
 }
 html, body{
   width: 100%;
   font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", "微软雅黑", sans-serif;
 }
 body{
   line-height: 1;
   -webkit-text-size-adjust: none;
   -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
 }
 html{
   overflow-y: scroll;
   font-size: 18px;
 }
  
 /*清除浮动*/
 .clearfix:before,
 .clearfix:after{
   content: " ";
   display: inline-block;
   height: 0;
   clear: both;
   visibility: hidden;
 }
 .clearfix{
   *zoom: 1;
 }
  
 /*隐藏*/
 .dn{
   display: none;
 }
 
 /* flex布局 */
/* flex布局 */
.flex_between {
  display: flex;
  justify-content: space-between;
}

.flex_center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex_bea {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex_align {
  display: flex;
  align-items: center;
}

.icon_img {
  width: 80px;
  height: 80px;
  border-radius: 8px;
  margin-right: 8px;
}
